.gameboard{
    background: #ccc;
}
.player{
    background: #000;
}
.tail-block{
    background: #3e3e3e;
}
.pickup{
    background: #3e3e;
}
.controls{
    display: flex;
    justify-content: left;
    font-size: 1.4rem;
    margin: 0;
    padding: 0;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.controls-table{
    width: 100%;
}

.controls-btn{
    width: 100%;
    height: 50px;
    margin: 0;
}
.modal{
    display: block;
    background: #fff;
    position: absolute;
    top: 0;
    left: 0;
}

.hidden{
    display: none;
}

.close{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.start-btn{
    -webkit-transition: 1s linear;
    transition: 1s linear;
}
